<template>
    <div class="purchase-transfer">
        <div class="ta-c fs-25">
            <p>Bn vui lng thanh ton chuyn khon vo thng tin sau:</p>
            <p> Ch ti khon : <strong>CNG TY CP M-TP TALENT</strong></p>
            <p> Ngn hng: <strong>TECHCOMBANK - Ngn hng TMCP K thng Vit Nam</strong></p>
            <p> S ti khon: <strong>19134215577888</strong></p>
            <p> Chi nhnh: <strong>Ph M Hng</strong></p>
        </div>

        <a href="#" class="ta-c" @click.prevent="$router.push({ name:'CheckOutDone' })">{{ ee('Next') }}</a>
    </div>
</template>

<script>
import $ from 'jquery';
import ee from '@/inc/lang';
import CheckoutModel from '@/models/checkout';

export default {
    data() {
        return {
            ee,
        }
    },

    computed: {
        userId() {
            return this.$store.state.userId;
        },

        user() {
            return this.$store.state.user;
        },

        username() {
            return this.$store.state.username || this.user.name;
        }
    },

    async mounted() {
        if (!this.userId) {
            return this.$router.go({ name: 'Index' });
        }

        var { status, data } = await CheckoutModel.useTransfer(this.username);
        if (status == 200) {
            this.$store.state.orderId = data;
            this.$store.state.checkoutMethod = 'transfer';
            // this.$router.push({ name: 'CheckOutDone' });
        }
    },

    methods: {

    }
}
</script>

<style scoped>
a {
    display: block;
    color: #888;
    margin-bottom: 20px;
}
</style>


